.gallery {
  width: 320px;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 0 auto;
  padding-top: 32px;
  display: flex;
}

.gallery__genres {
  color: #b92f2c;
}

.gallery__item {
  width: 280px;
  border-radius: 5px;
  margin-bottom: 16px;
  transition: box-shadow .25s;
  display: block;
  position: relative;
  overflow: hidden;
}

.gallery__item:hover {
  box-shadow: 0 5px 6px -1px rgba(0, 0, 0, .6);
}

.gallery__figure {
  width: 100%;
  height: 443px;
  color: #000;
  background-color: #fff;
  border-radius: 5px;
  position: relative;
}

.gallery__figure:hover .gallery__img {
  filter: grayscale();
  opacity: .8;
}

.gallery__img {
  width: 100%;
  height: 402px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  transition: filter .25s, opacity .25s;
}

.gallery__figcaption {
  width: 100%;
  text-align: left;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

.gallery__caption {
  white-space: nowrap;
  text-overflow: ellipsis;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  overflow: hidden;
}

.gallery__rating {
