.backdrop-modal {
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: rgba(0, 0, 0, .475);
  position: fixed;
  top: 0;
  left: 0;
}

.team__backdrop--hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.team__modal--close {
  width: 35px;
  height: 35px;
  cursor: pointer;
  background: none;
  border: 0;
  padding: 0;
  position: absolute;
  top: 20px;
  right: 20px;
}

.inner {
  width: inherit;
  text-align: center;
}

label {
  text-transform: uppercase;
  color: red;
  opacity: 0;
  cursor: pointer;
  font-size: .8em;
  line-height: 3em;
  transition: all .3s ease-in;
}

.inner:before, .inner:after {
  content: "";
  height: 2px;
  width: inherit;
  background: red;
  transition: all .3s ease-in;
  position: absolute;
  left: 0;
}

.inner:before {
  top: 50%;
  transform: rotate(45deg);
}

.inner:after {
  bottom: 50%;
  transform: rotate(-45deg);
}

.outer:hover label {
  opacity: 1;
}

.outer:hover .inner:before, .outer:hover .inner:after {
  transform: rotate(0);
}

.outer:hover .inner:before {
  top: 0;
}

.outer:hover .inner:after {
  bottom: 0;
}

